* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'montseratt', sans-serif;
}

.App {
  background-image: url('./assets/cold-bg.jpg');
  background-size: cover;
  background-position: bottom;
  transition: 0.4 ease-out;
}

.App.warm {
  background-image: url('./assets/warm-bg.jpg');
}

main {
  min-height: 100vh;
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.75));
  padding: 25px;
}

.search-box {
  width: 100%;
  margin: 0 0 75px;
}

.search-box .search-bar {
  display: block;
  width: 100%;
  padding: 15px;
  appearance: none;
  background: none;
  outline: none;
  border: none;
  background-color: rgba(255, 255, 255, 0.65);
  border-radius: 8px 8px 8px 8px;
  box-shadow: 0px 5px rgba(0, 0, 0, 0.2);
  color: #414141;
  opacity: .75;
  font-size: 20px;
  font-weight: 700;
  transition: 0.4s ease;
}

::placeholder {
  color: #414141;
  opacity: .75; /* Firefox */
}

:-ms-input-placeholder { /* Internet Explorer 10-11 */
  color: #414141;
  opacity: .75;
}

::-ms-input-placeholder { /* Microsoft Edge */
  color: #414141;
  opacity: .75;
}

.search-box .search-bar:focus {
  background-color: rgba(255, 255, 255, 0.8);
}

.location-box .location {
  color: white;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 2px 2px rgba(50, 50, 70, 0.5);
}

.location-box .date {
  color: white;
  font-size: 20px;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  text-shadow: 2px 2px rgba(50, 50, 70, 0.5);
}

.weather-box {
  text-align: center;
}

.weather-box .temp {
  position: relative;
  display: inline-block;
  margin: 30px auto;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 16px;
  padding: 15px 25px;
  color: white;
  font-size: 102px;
  font-weight: 900;
  text-shadow: 2px 4px rgba(50, 50, 70, 0.5);
  text-align: center;
  box-shadow: 2px 4px 5px 0px rgba(0, 0, 0, 0.2);
}

.weather-box .weather {
  color: white;
  font-size: 48px;
  font-weight: 700;
  text-shadow: 2px 2px rgba(50, 50, 70, 0.5);
}